import seModules from 'suneditor/src/plugins/modules'

const plugin_dialog = {
  name: 'sharepointFilesLink',
  display: 'dialog',
  title:'SharePoint files link', 
  buttonClass:'', 
  innerHTML:'<span>SPFL</span>', 
  rdSuffix: 'pluginDialong'+ Math.floor(Math.random() * 100) +'_', 
  
  // @Required
  // add function - It is called only once when the plugin is first run.
  // This function generates HTML to append and register the event.
  // arguments - (core : core object, targetElement : clicked button element)
  add: function (core) {
    core.addModule([seModules]);
    // @Required
    // Registering a namespace for caching as a plugin name in the context object
    const context = core.context;
    
    const sharepointFilesLinkLangs = {
      en: {
        dialogTitle: 'SharePoint file link', 
        loadingFiles: 'Loading...', 
        loadingFolders: 'Loading...', 
        selectFile: 'File', 
        selectFolder: 'Folder', 
        selectFileTooltip: 'Select a file to link to', 
        selectFolderTooltip: 'Select a folder to retrieve files from', 
      }, 
      fr: {
        dialogTitle: 'Lien vers un fichier SharePoint', 
        loadingFiles: 'Chargement...', 
        loadingFolders: 'Chargement...', 
        selectFile: 'Fichier', 
        selectFolder: 'Dossier', 
        selectFileTooltip: 'Slectionner un fichier cible', 
        selectFolderTooltip: 'Slectionner un rpertoire cible', 
      }
    }
    const langCode = sharepointFilesLinkLangs[core.lang.code.toLowerCase()] ? core.lang.code.toLowerCase() : 'en';
    core.lang.sharepointFilesLink = sharepointFilesLinkLangs[langCode];
    
    context.sharepointFilesLink = {
      focusElement: null, // @Override // This element has focus when the dialog is opened.
      filesListEl: null,
      targetCheckbox: null,
      linkAnchorText: null,
      _linkAnchor: null
    };
    
    console.log('core.lang:', core.lang);
    
    /** link dialog */
    let spFiles_dialog = this.setDialog.call(core);
    context.sharepointFilesLink.modal = spFiles_dialog;
    context.sharepointFilesLink.focusElement = spFiles_dialog.querySelector('._sp_folders_list_select');
    context.sharepointFilesLink.filesListEl = spFiles_dialog.querySelector('._sp_files_list_select');
    context.sharepointFilesLink.linkAnchorText = spFiles_dialog.querySelector('._se_link_text');
    context.sharepointFilesLink.targetCheckbox = spFiles_dialog.querySelector('._sp_link-new-tab');
    
    var foldersSelectEl = spFiles_dialog.querySelector('._sp_folders_list_select');
    
    let timeoutValue = Math.floor(Math.random() * (2000 - 1200 + 1) + 1200);
    setTimeout(() => { // TODO: Retrieve real folders list
      let fdList = this.loadFoldersList();
      foldersSelectEl.innerHTML = (fdList && fdList.length) ? '<option value="0">- Select -</option>'+ fdList.map((fd) => `<option value="${fd.id}">${fd.text}</option>`).join('') : '<option>None</option>';
      
      console.log('Fake data generated after', timeoutValue, 'ms.');
    }, timeoutValue);
    
    /** link controller */
    let link_controller = this.setController_LinkButton.call(core);
    context.sharepointFilesLink.linkController = link_controller;
    context.sharepointFilesLink._linkAnchor = null;
    
    // @Required
    // You must register the event preventDefault code in the "mousedown" event of the controller.
    const topThis = this;
    link_controller.addEventListener('mousedown', function (e) { console.log('Over here!'); e.preventDefault(); }, false);
    foldersSelectEl.addEventListener('change', function(e) { topThis.handleFolderChange(e, context); e.preventDefault(); }, false);
    
    /** add event listeners */
    spFiles_dialog.querySelector('.se-btn-primary').addEventListener('click', this.submit.bind(core));
    link_controller.addEventListener('click', this.onClick_linkController.bind(core));
    
    /** append html */
    context.dialog.modal.appendChild(spFiles_dialog);
    
    /** append controller */
    context.element.relative.appendChild(link_controller);
    
    /** empty memory */
    spFiles_dialog = null;
    link_controller = null;
  },

  /** dialog */
  setDialog: function () {
    const lang = this.lang;
    const rdSuffix = this.plugins.sharepointFilesLink.rdSuffix;
    const dialog = this.util.createElement('DIV');
    
    dialog.className = 'se-dialog-content';
    dialog.style.display = 'none';
    let html = '' +
      '<form class="editor_link">' +
        '<div class="se-dialog-header">' +
          '<button type="button" data-command="close" class="se-btn se-dialog-close" aria-label="Close" title="' + lang.dialogBox.close + '">' +
            this.icons.cancel +
          '</button>' +
          '<span class="se-modal-title">' + this.lang.sharepointFilesLink.dialogTitle + '</span>' +
        '</div>' +
        '<div class="se-dialog-body">' +
          
        '<div class="se-dialog-form">'+
            '<label title="'+ this.lang.sharepointFilesLink.selectFolderTooltip +'">'+ this.lang.sharepointFilesLink.selectFolder +'</label> &nbsp; ' +
            '<select class="se-input-select _sp_folders_list_select"><option>'+  +this.lang.sharepointFilesLink.loadingFolder +'</option></select> &nbsp; ' +
            '<label title="'+  this.lang.sharepointFilesLink.selectFileTooltip  +'">'+ this.lang.sharepointFilesLink.selectFile +'</label> &nbsp; ' +
            '<select class="se-input-select _sp_files_list_select"><option></option></select>' +
          '</div>' +
          '<div class="se-dialog-form">'+
            
          '</div>' +
          
          '<div class="se-dialog-form">' +
            '<label>' + lang.dialogBox.linkBox.text + '</label><input class="se-input-form _se_link_text" type="text" />' +
          '</div>' +
          '<div class="se-dialog-form se-dialog-form-footer">' +
            '<input id="'+ rdSuffix +'newTabLink" type="checkbox" class="_sp_link-new-tab" />&nbsp;<label for="'+ rdSuffix +'newTabLink">Open link in new window</label>' +
          '</div>' +
        '</div>' +
        '<div class="se-dialog-footer">' +
          '<button type="submit" class="se-btn-primary" title="' + lang.dialogBox.submitButton + '"><span>' + lang.dialogBox.submitButton + '</span></button>' +
        '</div>' +
      '</form>';
      
    dialog.innerHTML = html;
    
    return dialog;
  },
  
  /** modify controller button */
  setController_LinkButton: function () {
    const lang = this.lang;
    const icons = this.icons;
    const link_btn = this.util.createElement('DIV');
    
    link_btn.className = 'se-controller se-controller-link';
    link_btn.innerHTML = '' +
      '<div class="se-arrow se-arrow-up"></div>' +
      '<div class="link-content"><span><a target="_blank" href=""></a>&nbsp;</span>' +
        '<div class="se-btn-group">' +
          '<button type="button" data-command="update" tabindex="-1" class="se-tooltip">' +
            icons.edit +
            '<span class="se-tooltip-inner"><span class="se-tooltip-text">' + lang.controller.edit + '</span></span>' +
          '</button>' +
          '<button type="button" data-command="unlink" tabindex="-1" class="se-tooltip">' +
            icons.unlink +
            '<span class="se-tooltip-inner"><span class="se-tooltip-text">' + lang.controller.unlink + '</span></span>' +
          '</button>' +
          '<button type="button" data-command="delete" tabindex="-1" class="se-tooltip">' +
            icons.delete +
            '<span class="se-tooltip-inner"><span class="se-tooltip-text">' + lang.controller.remove + '</span></span>' +
          '</button>' +
        '</div>' +
      '</div>';

    return link_btn;
  },

  // @Required, @Override dialog
  // This method is called when the plugin button is clicked.
  // Open the modal window here.
  open: function () {
    // open.call(core, pluginName, isModify);
    this.plugins.dialog.open.call(this, 'sharepointFilesLink', 'sharepointFilesLink' === this.currentControllerName);
  },
  
  submit: function (e) {
    this.showLoading();
    
    e.preventDefault();
    e.stopPropagation();
    
    const submitAction = function () {
      if (this.context.sharepointFilesLink.focusElement.value.trim().length === 0) return false;
      
      const contextLink = this.context.sharepointFilesLink;
      const url = '#file-'+ contextLink.filesListEl.value;
      const anchor = contextLink.linkAnchorText;
      const anchorText = anchor.value.length === 0 ? url : anchor.value;
      
      // When opened for modification "this.context.dialog.updateModal" is true
      if (!this.context.dialog.updateModal) {
        const oA = this.util.createElement('A');
        oA.href = url;
        oA.textContent = anchorText;
        oA.target = contextLink.targetCheckbox.checked ? '_blank' : '';
        
        const selectedFormats = this.getSelectedElements();
        if (selectedFormats.length > 1) {
          const oFormat = this.util.createElement(selectedFormats[0].nodeName);
          oFormat.appendChild(oA);
          this.insertNode(oFormat);
        } else {
          this.insertNode(oA);
        }
        
        this.setRange(oA.childNodes[0], 0, oA.childNodes[0], oA.textContent.length);
      } else {
        contextLink._linkAnchor.href = url;
        contextLink._linkAnchor.textContent = anchorText;
        contextLink._linkAnchor.target = contextLink.targetCheckbox.checked ? '_blank' : '';
        
        // set range
        this.setRange(contextLink._linkAnchor.childNodes[0], 0, contextLink._linkAnchor.childNodes[0], contextLink._linkAnchor.textContent.length);
      }
      
      // history stack
      this.history.push(false);
      
      contextLink.focusElement.value = '';
      contextLink.linkAnchorText.value = '';
    }.bind(this);
    
    try {
      submitAction();
    } finally {
      this.plugins.dialog.close.call(this);
      this.closeLoading();
      this.focus();
    }
    
    return false;
  },

  // @Override core
  // Plugins with active methods load immediately when the editor loads.
  // Called each time the selection is moved.
  active: function (element) {
    if (!element) {
      if (this.controllerArray.indexOf(this.context.sharepointFilesLink.linkController) > -1) {
        this.controllersOff();
      }
    } else if (this.util.isAnchor(element) && element.getAttribute('data-image-link') === null) {
      if (this.controllerArray.indexOf(this.context.sharepointFilesLink.linkController) < 0) {
        this.plugins.sharepointFilesLink.call_controller.call(this, element);
      }
      return true;
    }

    return false;
  },

  // @Override dialog
  // This method is called just before the dialog opens.
  // If "update" argument is true, it is not a new call, but a call to modify an already created element.
  on: function (update) {
    if (!update) {
      this.plugins.sharepointFilesLink.init.call(this);
      this.context.sharepointFilesLink.linkAnchorText.value = this.getSelection().toString();
    } else if (this.context.sharepointFilesLink._linkAnchor) {
      // "update" and "this.context.dialog.updateModal" are always the same value.
      // This code is an exception to the "link" plugin.
      this.context.dialog.updateModal = true;
      this.context.sharepointFilesLink.focusElement.value = this.context.sharepointFilesLink._linkAnchor.href;
      this.context.sharepointFilesLink.linkAnchorText.value = this.context.sharepointFilesLink._linkAnchor.textContent;
      this.context.sharepointFilesLink.targetCheckbox.checked = this.context.sharepointFilesLink._linkAnchor.target ? true : false;
    }
  },

  call_controller: function (selectionATag) {
    this.editLink = this.context.sharepointFilesLink._linkAnchor = selectionATag;
    const linkBtn = this.context.sharepointFilesLink.linkController;
    const link = linkBtn.querySelector('a');

    link.href = selectionATag.href;
    link.title = selectionATag.textContent;
    link.textContent = selectionATag.textContent;

    const offset = this.util.getOffset(selectionATag, this.context.element.wysiwygFrame);
    linkBtn.style.top = (offset.top + selectionATag.offsetHeight + 10) + 'px';
    linkBtn.style.left = (offset.left - this.context.element.wysiwygFrame.scrollLeft) + 'px';

    linkBtn.style.display = 'block';

    const overLeft = this.context.element.wysiwygFrame.offsetWidth - (linkBtn.offsetLeft + linkBtn.offsetWidth);
    if (overLeft < 0) {
      linkBtn.style.left = (linkBtn.offsetLeft + overLeft) + 'px';
      linkBtn.firstElementChild.style.left = (20 - overLeft) + 'px';
    } else {
      linkBtn.firstElementChild.style.left = '20px';
    }
    
    // Show controller at editor area (controller elements, function, "controller target element(@Required)", "controller name(@Required)", etc..)
    this.controllersOn(linkBtn, selectionATag, 'sharepointFilesLink');
  },

  onClick_linkController: function (e) {
    console.log('Xouxou');
    e.stopPropagation();
    
    const command = e.target.getAttribute('data-command');
    if (!command) return;

    e.preventDefault();

    if (/update/.test(command)) {
      const contextLink = this.context.sharepointFilesLink;
      contextLink.focusElement.value = contextLink._linkAnchor.href;
      contextLink.linkAnchorText.value = contextLink._linkAnchor.textContent;
      this.plugins.dialog.open.call(this, 'sharepointFilesLink', true);
    }
    else if (/unlink/.test(command)) {
      const sc = this.util.getChildElement(this.context.sharepointFilesLink._linkAnchor, function (current) { return current.childNodes.length === 0 || current.nodeType === 3; }, false);
      const ec = this.util.getChildElement(this.context.sharepointFilesLink._linkAnchor, function (current) { return current.childNodes.length === 0 || current.nodeType === 3; }, true);
      this.setRange(sc, 0, ec, ec.textContent.length);
      this.nodeChange(null, null, ['A'], false);
    }
    else {
      /** delete */
      this.util.removeItem(this.context.sharepointFilesLink._linkAnchor);
      this.context.sharepointFilesLink._linkAnchor = null;
      this.focus();

      // history stack
      this.history.push(false);
    }

    this.controllersOff();
  },
  
  // @Required, @Override dialog
  // This method is called when the dialog window is closed.
  // Initialize the properties.
  init: function () {
    const contextLink = this.context.sharepointFilesLink;
    contextLink.linkController.style.display = 'none';
    contextLink._linkAnchor = null;
    contextLink.focusElement.value = '';
    contextLink.linkAnchorText.value = '';
    contextLink.targetCheckbox.checked = false;
  }, 
  
  loadFoldersList: function() {
    return Array.from([1, 2, 3], id => { return {id: id, text: 'Folder '+ id} });
  }, 
  
  loadFilesList: function() {
    return Array.from([Math.floor(Math.random() * (70 - 65 + 1) + 65), Math.floor(Math.random() * (75 - 70 + 1) + 70), Math.floor(Math.random() * (80 - 75 + 1) + 75)], id => { return {id: id, text: 'File '+ String.fromCharCode(id)} });
  }, 
  
  handleFolderChange: function(evt, seContext) {
    evt.preventDefault();
    let filesList = this.loadFilesList();
    seContext.sharepointFilesLink.filesListEl.innerHTML = filesList && filesList.length ? '<option value="0">- File -</option>'+ filesList.map((fd) => `<option value="${fd.id}">${fd.text}</option>`).join('') : '<option>None</option>';
    console.log('handleFolderChange: ', seContext.sharepointFilesLink.focusElement, '=>', seContext.sharepointFilesLink.focusElement.value);
  }
};

export default plugin_dialog;